<template>
  <div class="collect">
    <div @click="toessay(item.aid)" v-for="(item,index) in collect" :key="index" class="all-item">
      <div class="all-top">
        <div class="all-left">
          <div class="titlebar">
            <div class="title"><span class="date">{{item.dateline|dayFmt}}</span>{{item.title}}</div>
          </div>
          <div class="opt-box">
            <span>{{item.ansnum}}回答</span>
            <span>{{item.comment}}收藏</span>
          </div>
        </div>
        <div class="all-img" v-if="item.img">
          <img :src="item.head" alt="">
        </div>
      </div>
      <div class="all-bottom">
        <div class="img">
          <img src="@/images/write.png" alt="">
        </div>
        回答
      </div>
    </div>
  </div>
</template>

<script>
  export default {
    name: "collect",
    props: {
      collect: Array,
    },
    data() {
      return {
        lists: []
      }
    },
    mounted() {
      // this.getdata()
    },
    methods: {
      //  跳往页面详情
      toessay(aid) {
        this.$router.push({path: '/essay',query:{aid}})
      },
      // getdata() {
      //   this.$post('http://localhost:8080/news/article').then(res => {
      //     // console.log(res);
      //     this.lists = res.data.article;
      //   }).catch(err => {
      //     console.log(err);
      //   })
      // },
    }
  }
</script>

<style scoped lang="scss">
  $main: #409eff !default;
  .collect {
    display: flex;
    flex-direction: column;
    width: 100%;
    padding: 0 24px;
    box-sizing: border-box;

    .all-item {
      cursor: pointer;
      width: 100%;
      min-height: 122px;
      display: flex;
      flex-direction: column;
      justify-content: space-around;
      border-bottom: 1px solid #eee;
      padding: 10px 0;

      .all-top {
        display: flex;
        justify-content: space-between;

        .all-left {
          .titlebar {
            display: flex;
            width: 464px;
            font-size: 20px;
            font-weight: bold;
            line-height: 30px;

            .title {
              display: -webkit-box;
              -webkit-box-orient: vertical;
              -webkit-line-clamp: 2;
              overflow: hidden;

              .date {
                color: $main;
              }
            }
          }

          .opt-box {
            font-size: 12px;
            font-weight: 400;
            color: rgba(153, 153, 153, 1);
            line-height: 30px;

            span {
              margin-right: 19px;
            }
          }
        }

        .all-img {
          width: 128px;
          height: 84px;
          border-radius: 6px;
          overflow: hidden;
          display: flex;
          align-items: center;

          img {

            width: 100%;
            height: 100%;
          }
        }
      }

      .all-bottom {
        display: flex;
        justify-content: flex-start;
        font-size: 14px;
        font-weight: 400;
        color: rgba(0, 150, 255, 1);
        line-height: 44px;

        .img {
          width: 15px;
          height: 15px;
          margin-right: 10px;

          > img {
            width: 100%;
            height: 100%;
            vertical-align: middle;
          }
        }
      }
    }
  }
</style>



