const $api = {
  //点赞
  like( aid, title, uid, username) {
    $post('index/data/like', { aid, title, uid, username}).then(res => {
      if (res.data.result === 200) {
        Message({type: "success", message: res.data.msg})
      } else {
        Message({type: "warning", message: res.data.msg})
      }
    }).catch(err => {
      console.log(err);
    })
  },
  //收藏
  collect(uid, username, aid, title,) {
    $post('index/data/question_collect', {uid, username, aid, title}).then(res => {
      if (res.data.result === 200) {
        Message({type: "success", message: res.data.msg})
      } else if (res.data.result === 201) {
        Message({type: "success", message: res.data.msg})
      } else {
        Message({type: "warning", message: res.data.msg})
      }
    }).catch(err => {
      console.log(err);
    })
  }
};


/**
 * 封装get方法
 * @param url
 * @param data
 * @returns {Promise}
 */
export function $get(url, data = {}) {
  return new Promise((resolve, reject) => {
    ajax.get(url, {
      params: data
    })
      .then(response => {
        resolve(response);
      })
      .catch(err => {
        reject(err)
      })
  })
}


/**
 * 封装post请求
 * @param url
 * @param data
 * @returns {Promise}
 */

export function $post(url, data = {}) {
  return new Promise((resolve, reject) => {
    ajax.post(url, data)
      .then(response => {
        resolve(response);
      }, err => {
        reject(err)
      })
  })
}

export {
  $httpUrl, $api
}



