<template>
  <div class="answer">
    <div class="title-box">
      <div class="title">等我来答</div>
      <!--<router-link to="#" class="more">更多</router-link>-->
    </div>
    <ul class="ans-box">
      <li @click="toessay(item.aid)" v-for="(item,index) in answer" :key="index" class="ans-item">
        <div class="ans-title">{{item.title}}</div>
        <div class="ans">
          <img src="@/images/write.png" alt="">
          回答
        </div>
      </li>
    </ul>
  </div>
</template>

<script>
    export default {
        name: "answer",
      data(){
          return{
            answer: [],
          }
      },

      mounted() {

        this.getarticle();

      },
      methods: {
        //  跳往页面详情
        toessay(aid) {
          this.$router.push({path: '/essay',query:{aid}})
        },
        //获取文章数据
        getarticle() {
          this.$post("index/data/question_list").then(res => {
            this.answer = res.data;
          }).catch(err => {
            console.log(err);
          })
        },
        //tab切换

      }
    }
</script>

<style scoped lang="scss">
  .answer {
    margin-top: 20px;
    width: 100%;
    background: rgba(255, 255, 255, 1);
    box-shadow: 0px 2px 9px 0px rgba(0, 0, 0, 0.12);
    border-radius: 6px;
    display: flex;
    flex-direction: column;

    .title-box {
      display: flex;
      justify-content: space-between;
      padding: 0 22px;
      box-sizing: border-box;
      height: 44px;
      line-height: 44px;
      border-bottom: 1px solid rgba(221, 221, 221, 1);

      .title {
        font-size: 16px;
        font-weight: 400;
        color: rgba(51, 51, 51, 1);
        line-height: 44px;
        position: relative;

        &:after {
          content: '';
          width: 100%;
          height: 4px;
          background: rgba(0, 150, 255, 1);
          border-radius: 2px;
          position: absolute;
          bottom: 0;
          left: 0;

        }
      }

      .more {
        width: 30px;
        height: 15px;
        font-size: 14px;
        font-weight: 400;
        color: rgba(153, 153, 153, 1);
        line-height: 44px;
      }
    }

    .ans-box {
      padding: 0 20px;

      .ans-item {
        cursor: pointer;
        width: 100%;
        height: 84px;
        display: flex;
        justify-content: center;
        flex-direction: column;

        .ans-title {
          overflow: hidden;
          white-space: nowrap;
          text-overflow: ellipsis;
          width: 270px;
          font-size: 14px;
          font-weight: 400;
          color: rgba(51, 51, 51, 1);
          line-height: 40px;
        }

        .ans {
          img {
            width: 15px;
            height: 15px;
            margin-right: 10px;
          }
        }
      }
    }
  }
</style>



