<template>
  <div class="articles">
    <ul>
      <li v-for="(item,index) in article" :key="index" class="art-item">
        <div class="title" @click="toessay(item.aid)">{{item.title}}</div>
        <div class="art-box">
          <div class="art-left">
            <div class="info">{{item.commentnum}}回答 &nbsp;&nbsp;{{item.favtimes}}收藏</div>
            <div class="user" @click="touser(item.uid)">
              <div class="head"><img :src="item.head" alt="用户头像"></div>
              <div class="username">{{item.username}}</div>
              {{item.label}}
              <!--<ul class="labes" v-for="(el,i) in item.labels" :key="i">
                <li class="label">{{el.label}}</li>
              </ul>-->
            </div>
            <div class="describe" v-html="item.list.content">

            </div>
          </div>
          <div class="art-right" v-if="item.pic1">
            <img :src="item.pic1" alt="">
          </div>
        </div>
        <div class="operate">
             <span class="exist" v-if="item.list.islike==1"><i
               class="iconfont icondianzan"
               style="margin-right: 5px;"></i>{{item.list.likenum}}赞</span>
          <span v-else @click="tolike(item.list.id,item.title,item.uid,item.username)" class="praise"><i
            class="iconfont icondianzan"
            style="margin-right: 5px;"></i>{{item.list.likenum}}赞</span>

          <!--<span class="exist" v-if="item.list.isrubbish==1"><i class="iconfont iconyijin14-cai"-->
                                                          <!--style="margin-right: 5px;"></i>踩</span>-->
          <!--<span class="tread" v-else @click="torubbish(item.list.id,item.title,item.uid,item.username)"><i-->
            <!--class="iconfont iconyijin14-cai" style="margin-right: 5px;"></i> 踩</span>-->
          <span class="tread"  @click="torubbish(item.list.id,item.title,item.uid,item.username)"><i
          class="iconfont iconyijin14-cai" style="margin-right: 5px;"></i> 踩</span>
          <span class="comment" @click="toessay(item.aid)" style="cursor: pointer"><img src="@/images/comment.png"
                                                                                        alt="">{{item.list.commentnum}}评价</span>
          <div class="share">
            <div class="share-word">
              <img src="@/images/share.png" alt="">分享
            </div>
            <div class="share-way">
              <span class="weixin"><img src="@/images/weixin.png" alt="">
              <span class="qr-box">
                <div class="qr">
                   <canvas :id="item.aid"></canvas>
                </div>
              </span>
              </span>
              <span class="weibo" @click="toweibo(item.title,item.aid)"><img src="@/images/weibo.png" alt=""></span>
            </div>
          </div>
        </div>
      </li>
    </ul>
  </div>
</template>

<script>
  // 引入qrcode
  import QRCode from 'qrcode'
  import {mapState} from 'vuex'
  export default {
    name: "articles",
    data() {
      return {
      };
    },
    props: {
      article: Array
    },
    watch: {
      article: {
        handler: function (newValue, oldValue) {
          this.setqr();
        },
        deep: true,
      }
    },
    mounted() {
      this.init();
      this.setqr();
    },
    computed: mapState(
      ['isLogin','userInfo'],
    ),
    active() {
      this.setqr();
    },
    methods: {
      init() {
        // this.article=this.articles;
        this.article.map((value, index, array) => {
          value.head = this.$httpUrl + "/uc_server/avatar.php?uid=" + value.uid + "&amp;size=big"
        });
        this.article.slice();
      },
      //用户详情
      touser(uid) {
        this.$router.push({name: 'user', query: {otheruid: uid}})
      },
      //踩
      torubbish(aid, title, uid, username) {
        if (this.isLogin||this.userInfo) {
          this.$post('index/data/rubbish', {aid, title, uid, username}).then(res => {
            if (res.data.result === 200) {
              this.$emit('isrubbish',true);
              this.$message.success(res.data.msg);
            } else if (res.data.result === 201) {
              this.$message.warning(res.data.msg);
            } else {
              this.$message.error(res.data.msg);
            }
          })
        } else {
          this.$message('请登录后再操作');
          setTimeout(() => {
            this.$router.push('/loginmima');
          }, 1000);
        }
      },
      //二维码
      setqr() {
        setTimeout(() => {
          this.article.map((value, index, arr) => {
            let id = value.aid;
            let val = document.domain + "/essay?aid=" + value.aid;
            this.qr(val, id);
          })
        }, 500)
      },
      qr(val, id) {
        var msg = document.getElementById(id);
        // 将获取到的数据（val）画到msg（canvas）上
        QRCode.toCanvas(msg, val, function (error) {
          // console.log(error)
        });


      },
      //  跳往页面详情
      toessay(aid) {
        this.$router.push({path: '/essay', query: {aid}})
      },

      //点赞
      tolike(aid, title, uid, username) {
        if (this.isLogin||this.userInfo) {
          this.$post('index/data/like', { aid, title, uid, username}).then(res => {
            if (res.data.result === 200) {
              this.$emit('islike',true);
              this.$message({type: "success", message: res.data.msg})
            } else {
              this.$message({type: "warning", message: res.data.msg})
            }
          }).catch(err => {
            console.log(err);
          })
        } else {
          this.$message('请登录后再评论');
          setTimeout(() => {
            this.$router.push('/loginmima');
          }, 1000);
        }
      },
      //分享到微博
      toweibo(title, aid) {
        // let url=document.domain+'/essay?aid='+aid;
        let url = this.$httpUrl + '/template/quater_10_qkl/src/logo.png';
        let picurl = this.$httpUrl + '/template/quater_10_qkl/src/logo.png';
        var sharesinastring = 'http://v.t.sina.com.cn/share/share.php?title=' + title + '&url=' + url + '&content=utf-8&sourceUrl=' + url + '&pic=' + picurl;
        window.open(sharesinastring, 'newwindow', 'height=600,width=800,top=100,left=100');
      }
    }
  }
</script>

<style scoped lang="scss">
  $main: #409eff !default;
  .articles {
    > ul {
      width: 100%;
      display: flex;
      flex-direction: column;

      .art-item {
        height: 238px;
        background: rgba(255, 255, 255, 1);
        box-shadow: 0px 2px 9px 0px rgba(0, 0, 0, 0.12);
        border-radius: 6px;
        display: flex;
        flex-direction: column;
        padding: 22px 30px 20px 24px;
        box-sizing: border-box;
        margin-bottom: 20px;

        &:hover .title {
          color: $main;
        }

        .title {
          cursor: pointer;
          width: 550px;
          font-size: 20px;
          font-weight: bold;
          color: rgba(51, 51, 51, 1);
          line-height: 24px;
          overflow: hidden;
          white-space: nowrap;
          text-overflow: ellipsis;
          height: 30px;
        }

        .art-box {
          display: flex;
          justify-content: space-between;

          .art-left {
            width: 546px;

            .info {
              margin: 14px 0;
              font-size: 12px;
              color: rgba(153, 153, 153, 1);
              line-height: 18px;


            }

            .user {
              display: flex;
              justify-content: flex-start;
              cursor: pointer;

              .head {
                width: 20px;
                height: 20px;
                border-radius: 50%;
                overflow: hidden;

                > img {
                  width: 100%;
                  height: 100%;
                }
              }

              .username {
                margin-left: 10px;
                padding-right: 20px;
                min-width: 50px;
                color: rgba(51, 51, 51, 1);
                line-height: 20px;
              }

              .labes {
                font-weight: 400;
                color: rgba(102, 102, 102, 1);
                line-height: 20px;
                display: flex;
                justify-content: flex-start;

                .label {
                  margin-right: 20px;
                }
              }

            }

            .describe {
              margin-top: 14px;
              font-size: 14px;
              font-weight: 400;
              height: 60px;
              color: rgba(153, 153, 153, 1);
              line-height: 20px;
              display: -webkit-box;
              -webkit-box-orient: vertical;
              -webkit-line-clamp: 3;
              overflow: hidden;
            }
          }

          .art-right {
            > img {
              width: 134px;
              height: 134px;
              border-radius: 4px;
              overflow: hidden;
            }
          }
        }


        .operate {
          margin-top: 10px;
          font-size: 14px;
          font-weight: 400;
          color: #999999;
          line-height: 20px;
          height: 30px;
          display: flex;
          align-items: center;

          .exist {
            color: $main;
          }

          span {
            margin-right: 30px;
          }

          .praise, .tread {
            &:hover {
              color: #cacaca;
            }

            cursor: pointer;
          }

          .comment {
            &:hover {
              color: #cacaca;
            }
          }

          .share {
            display: flex;
            justify-content: flex-start;

            &:hover {
              color: #cacaca;
            }

            &:hover .share-way {
              display: inline-block;
            }

            .share-way {
              margin-left: 26px;
              display: none;

              .weixin {
                position: relative;

                &:hover .qr-box {
                  display: flex;
                }

                .qr-box {
                  width: 94px;
                  height: 94px;
                  position: absolute;
                  left: 50%;
                  bottom: 20px;
                  transform: translateX(-50%);
                  background-color: red;
                  display: none;
                  justify-content: center;
                  align-items: center;
                  background: rgba(255, 255, 255, 1);
                  box-shadow: 0px 3px 12px 0px rgba(0, 0, 0, 0.22);
                  border-radius: 4px;

                  .qr {
                    width: 85px;
                    height: 85px;
                    border: 1px dashed #000;

                    > canvas {
                      width: 100% !important;
                      height: 100% !important;
                    }
                  }
                }
              }

              .weibo {
                cursor: pointer;
              }
            }
          }

          img {
            width: 15px;
            vertical-align: middle;
            margin-right: 5px;
          }
        }
      }
    }
  }
</style>



