<template>
  <div>
    <div v-html="content" ref="editor" @input="result" id="editor" style="text-align:left"></div>
  </div>
</template>

<script>
  import E from 'wangeditor'

  export default {
    name: 'editor',
    props: ['content', 'path'],
    data() {
      return {
        editorContent: '',
        updata: true,
      }
    },
    methods: {
      init() {
        console.log("富文本初始化");
        var editor = new E(this.$refs.editor);

        editor.customConfig.onchange = (html) => {
          this.editorContent = html
        };
        // editor.customConfig.menus = ['head', 'bold', 'underline', '|', 'quote', 'unorderlist', 'orderlist', '|', 'eraser', 'fontfamily', 'fontsize',
        //   '|', 'img', 'video', '|', 'undo', 'redo', 'fullscreen'
        // ],

          editor.customConfig.debug = true;
        editor.customConfig.zIndex = 100;
        editor.customConfig.uploadImgServer = 'http://www.yeschains.com/index/Update/upload';
        editor.customConfig.uploadFileName = 'image';
        editor.customConfig.linkImgCallback = function (url) {
          // console.log('即插入图片的地址');
          // console.log(url) // url 即插入图片的地址
        };
        editor.customConfig.uploadImgHooks = {
          before: function (xhr, editor, files) {
            // 图片上传之前触发
            // xhr 是 XMLHttpRequst 对象，editor 是编辑器对象，files 是选择的图片文件

            // 如果返回的结果是 {prevent: true, msg: 'xxxx'} 则表示用户放弃上传
            // return {
            //     prevent: true,
            //     msg: '放弃上传'
            // }
          },
          success: function (xhr, editor, result) {
            // 图片上传并返回结果，图片插入成功之后触发
            // xhr 是 XMLHttpRequst 对象，editor 是编辑器对象，result 是服务器端返回的结果
          },
          fail: function (xhr, editor, result) {
            // 图片上传并返回结果，但图片插入错误时触发
            // xhr 是 XMLHttpRequst 对象，editor 是编辑器对象，result 是服务器端返回的结果
          },
          error: function (xhr, editor) {
            // 图片上传出错时触发
            // xhr 是 XMLHttpRequst 对象，editor 是编辑器对象
          },
          timeout: function (xhr, editor) {
            // 图片上传超时时触发
            // xhr 是 XMLHttpRequst 对象，editor 是编辑器对象
          },

          // 如果服务器端返回的不是 {errno:0, data: [...]} 这种格式，可使用该配置
          // （但是，服务器端返回的必须是一个 JSON 格式字符串！！！否则会报错）
          customInsert: function (insertImg, result, editor) {
            // 图片上传并返回结果，自定义插入图片的事件（而不是编辑器自动插入图片！！！）
            // insertImg 是插入图片的函数，editor 是编辑器对象，result 是服务器端返回的结果

            // 举例：假如上传图片成功后，服务器端返回的是 {url:'....'} 这种格式，即可这样插入图片：
            var url = result.url;
            // insertImg(url)
            insertImg(url)

            // result 必须是一个 JSON 格式字符串！！！否则报错
          }
        };
        // editor.customConfig.uploadImgShowBase64 = true ;  // 使用 base64 保存图片
        editor.create();
        this.updata = false;
        this.editor = editor;
      },
      result() {
        this.$emit('input', this.editor.txt.html())
      },
      //清除内容
      clearHtml() {
        this.editor.txt.clear()
      },
      //获取网页内容
      getHtml() {
        console.log(this.editor.txt.html());
      }
    },
    create() {
      // setTimeout(()=>{
      //   console.log("创建时");
      //   this.init();
      // },800)
    },
    mounted() {

      setTimeout(() => {

        if (this.updata) {
          this.init();
          console.log("页面加载");
          this.updata = false;
        }

      }, 600)
    },
    updated() {
      setTimeout(() => {
        if (this.updata) {
          this.init();
          console.log("页面更新");
        }
        this.updata = true;
      }, 600)
    },
    active() {
      // setTimeout(()=>{
      //   console.log(333333333);
      //   this.init();
      // },800)
    },
    // destroyed(){
    //   console.log("销毁");
    //   this.editor.destroy();
    // }
  }
</script>

<style scoped lang="scss">
  #editor {
    min-height: 150px;
    height: auto !important;
    /*max-height: 400px;*/
  }

  .wangEditor-container {
    border-radius: 2px;
    overflow: hidden;
    border: 1px solid #CCC;
  }

  #editor /deep/ .w-e-text-container {
    height: 200px !important;
  }
</style>



