//  将值存入Session的public中, name可为string或者object
export function session(name,value){
  if(arguments.length<2){//当 参数为 1 个时，取值 key 为 name 的值。当参数为 2 个时，设值 key 为name的值为 value。
    let obj = '';
    if(sessionStorage.getItem("public")){
      obj = JSON.parse(sessionStorage.getItem("public"))[name];
    }
    return obj;
  }else {
    let obj = sessionStorage.getItem("public") ? JSON.parse(sessionStorage.getItem("public")) : {};
    //判断name类型：
    if(typeof name ==='string'){
      obj[name] = value || '';
    }else if(typeof name ==='object'){
      name.forEach((item,i)=>{
        obj[i] = item || '';
      });
    }
    sessionStorage.setItem("public",JSON.stringify(obj));
  }
}
//删除sessionStorage存储数据
export const removeSession = (name) => {
  if (!name) return;
  window.sessionStorage.removeItem(name);
  window.sessionStorage.clear();
};
export function localStorage(name,value){
  if(arguments.length<2){//当 参数为 1 个时，取值 key 为 name 的值。当参数为 2 个时，设值 key 为name的值为 value。
    let obj = '';
    if(localStorage.getItem("public")){
      obj = JSON.parse(localStorage.getItem("public"))[name];
    }
    return obj;
  }else {
    let obj = localStorage.getItem("public") ? JSON.parse(localStorage.getItem("public")) : {};
    //判断name类型：
    if(typeof name ==='string'){
      obj[name] = value || '';
    }else if(typeof name ==='object'){
      name.forEach((item,i)=>{
        obj[i] = item || '';
      });
    }
    localStorage.setItem("public",JSON.stringify(obj));
  }
}
//删除localStorage存储数据
export const removeLocalStorage = (name) => {
  if (!name) return
  window.localStorage.removeItem(name)
};
//获取cookie、
export function getCookie(name) {
  var arr, reg = new RegExp("(^| )" + name + "=([^;]*)(;|$)");
  if (arr = document.cookie.match(reg))
    return (arr[2]);
  else
    return null;
}

//设置cookie,增加到vue实例方便全局调用
export function setCookie (c_name, value, expiredays) {
  var exdate = new Date();
  exdate.setDate(exdate.getDate() + expiredays);
  document.cookie = c_name + "=" + escape(value) + ((expiredays == null) ? "" : ";expires=" + exdate.toGMTString());
};

//删除cookie
export function delCookie (name) {
  var exp = new Date();
  exp.setTime(exp.getTime() - 1);
  var cval = getCookie(name);
  if (cval != null)
    document.cookie = name + "=" + cval + ";expires=" + exp.toGMTString();
};



