import Vue from 'vue'
import Vuex from 'vuex'
import {delCookie, getCookie, localStorage, removeSession, session, setCookie} from '@/unity/storage'

Vue.use(Vuex);

const state = {
  isLogin: false,
  userInfo: {},
  changePass: false,//是否需要改密码
  token: '',
  path: ''
};


const getters = {
  getUserInfo(state) {
    if (state.userInfo) {
      state.userInfo = JSON.parse(window.localStorage.getItem('userInfo'));
      state.isLogin = JSON.parse(window.localStorage.getItem('isLogin'));
    } else {
      return state.userInfo;
    }
  },
  getIsLogin() {
    return state.isLogin || session('isLogin');
  },
  getChangePass() {
    return state.changePass;
  },
  getPath() {
    return state.path || session('path');
  }
};


const mutations = {
  setLogin(state, userInfo) {
    // session('userInfo', userInfo);
    window.localStorage.setItem("userInfo", JSON.stringify(userInfo));
    window.localStorage.setItem("isLogin", true);
    state.userInfo = userInfo;
    state.isLogin = true;
  },
  setChangePass(state, changePass) {
    state.changePass = changePass;
  },
  setPath(state, path) {
    // session('path', path);
    window.localStorage.setItem("path", JSON.stringify(path));

    state.path = path;
  },
  removeSessionStor(state) {
    // removeSession('userInfo');
    window.localStorage.clear();
    if (state.userInfo) {
      delete state.userInfo
    }
    state.isLogin = false;


    // console.log(state);
    // let index=state.indexOf(name);
    // if(index>-1){
    //     this.splice(index,1);
    //     }
    // console.log();
  }
};

export default new Vuex.Store({
  state,
  getters,
  mutations
})



