// 引入mockjs
const Mock = require('mockjs');
// 获取 mock.Random 对象
const Random = Mock.Random;
// 首页tab栏标签

// Mock.mock( url, post/get , 返回的数据)；
// 首页tab栏标签
Mock.mock('http://localhost:8080/news/tabs', 'post', [{"name": '热门'}, {"name": '社会'}, {"name": '娱乐'}, {"name": '体育'}, {"name": '军事'}, {"name": '汽车'}, {"name": '财经'}, {"name": '科技'}, {"name": '育儿'},]
);
//文章数据
Mock.mock('http://localhost:8080/news/article', 'post', {
  "article|5-15": [
    {
      title: '@cparagraph',
      username: '@cname()',
      ansnum: '@natural(60, 100)',
      attnum: '@natural(60, 100)',
      "header|1": [
        'http://img.qqzhi.com/uploads/2018-12-23/085251409.jpg',
        'https://ss2.bdstatic.com/70cFvnSh_Q1YnxGkpoWK1HF6hhy/it/u=2175221156,3353011738&fm=27&gp=0.jpg',
        'https://ss1.baidu.com/6ONXsjip0QIZ8tyhnq/it/u=2720646439,3097064748&fm=85&app=63&f=JPEG?w=121&h=75&s=1591713342F866378C9CD08E030030A2',
        'http://i1.umei.cc/uploads/tu/201608/161/c16.jpg',
      ],
      "labels|1-2": [
        {label: '@ctitle(3)'},
      ],
      date: Random.date('MM月dd日'),
      dec: '@cparagraph',
      praise: '@natural(100, 1000)',
      comment: '@natural(60, 100)',
      "img|1": [
        'http://img.qqzhi.com/uploads/2018-12-23/085251409.jpg',
        'https://ss2.bdstatic.com/70cFvnSh_Q1YnxGkpoWK1HF6hhy/it/u=2175221156,3353011738&fm=27&gp=0.jpg',
        'https://ss1.baidu.com/6ONXsjip0QIZ8tyhnq/it/u=2720646439,3097064748&fm=85&app=63&f=JPEG?w=121&h=75&s=1591713342F866378C9CD08E030030A2',
        'http://i1.umei.cc/uploads/tu/201608/161/c16.jpg',
      ]
    }
  ]
});
//用户信息 回答用户列表
Mock.mock('http://localhost:8080/news/user', 'post', {
  name: '@cname()',
  "header|1": [
    'http://img.qqzhi.com/uploads/2018-12-23/085251409.jpg',
    'https://ss2.bdstatic.com/70cFvnSh_Q1YnxGkpoWK1HF6hhy/it/u=2175221156,3353011738&fm=27&gp=0.jpg',
    'https://ss1.baidu.com/6ONXsjip0QIZ8tyhnq/it/u=2720646439,3097064748&fm=85&app=63&f=JPEG?w=121&h=75&s=1591713342F866378C9CD08E030030A2',
    'http://i1.umei.cc/uploads/tu/201608/161/c16.jpg',
  ],
  age: '@natural(18, 50)',
  fans: '@natural(0, 300)',
  agree: '@natural(0, 300)',
  answer: '@natural(0, 300)',
  dec: '@cparagraph',
  title: '@ctitle(3)',
  date: Random.date('yyyy/mm/dd hh:mm')


});
//用户信息 用户回答列表
Mock.mock('http://localhost:8080/news/ans', 'post', {
  "ans|3-6": [{
    name: '@cname()',
    "header|1": [
      'http://img.qqzhi.com/uploads/2018-12-23/085251409.jpg',
      'https://ss2.bdstatic.com/70cFvnSh_Q1YnxGkpoWK1HF6hhy/it/u=2175221156,3353011738&fm=27&gp=0.jpg',
      'https://ss1.baidu.com/6ONXsjip0QIZ8tyhnq/it/u=2720646439,3097064748&fm=85&app=63&f=JPEG?w=121&h=75&s=1591713342F866378C9CD08E030030A2',
      'http://i1.umei.cc/uploads/tu/201608/161/c16.jpg',
    ],
    age: '@natural(18, 50)',
    fans: '@natural(0, 300)',
    agree: '@natural(0, 300)',
    answer: '@natural(0, 300)',
    dec: '@cparagraph',
    title: '@ctitle(10,100)',
    date: Random.date('yyyy/mm/dd hh:mm')


  }]
});
//文章详情页面
Mock.mock('http://localhost:8080/news/detail', 'post',
  {
    title: '@cparagraph',
    username: '@cname()',
    ansnum: '@natural(60, 100)',
    attnum: '@natural(60, 100)',
    "header|1": [
      'http://img.qqzhi.com/uploads/2018-12-23/085251409.jpg',
      'https://ss2.bdstatic.com/70cFvnSh_Q1YnxGkpoWK1HF6hhy/it/u=2175221156,3353011738&fm=27&gp=0.jpg',
      'https://ss1.baidu.com/6ONXsjip0QIZ8tyhnq/it/u=2720646439,3097064748&fm=85&app=63&f=JPEG?w=121&h=75&s=1591713342F866378C9CD08E030030A2',
      'http://i1.umei.cc/uploads/tu/201608/161/c16.jpg',
    ],
    "labels|1-2": [
      {label: '@ctitle(3)'},
    ],
    dec: '@cparagraph',
    praise: '@natural(100, 1000)',
    comment: '@natural(60, 100)',
    date: Random.date('MM-dd'),
    "img|1": [
      "https://p1.pstatp.com/list/190x124/174f5000042cd3e902087",
      "https://p3.pstatp.com/list/190x124/db06000794cdd67dca07",
      "http://p9.pstatp.com/list/190x124/113e900049ae0ea724a11",
      "https://p3.pstatp.com/list/190x124/113ea00073a546de659c6",
    ]
  }
);
//问题回答
Mock.mock('http://localhost:8080/news/answer', 'post', {
  "answer|5-15": [
    {
      name: '@cname()',
      "header|1": [
        'http://img.qqzhi.com/uploads/2018-12-23/085251409.jpg',
        'https://ss2.bdstatic.com/70cFvnSh_Q1YnxGkpoWK1HF6hhy/it/u=2175221156,3353011738&fm=27&gp=0.jpg',
        'https://ss1.baidu.com/6ONXsjip0QIZ8tyhnq/it/u=2720646439,3097064748&fm=85&app=63&f=JPEG?w=121&h=75&s=1591713342F866378C9CD08E030030A2',
        'http://i1.umei.cc/uploads/tu/201608/161/c16.jpg',
      ],
      age: '@natural(18, 50)',
      fans: '@natural(0, 300)',
      praise: '@natural(0, 300)',
      comment: '@natural(0, 300)',
      agree: '@natural(0, 300)',
      answer: '@cparagraph(50)',
      dec: '@cparagraph',
      time: '@date("yyyy-MM-dd hh:mm")',
      "labels|1": [{label: '历史领域创作者'}, {label: '文化先锋'}],
      "comments|10": [
        {
          "header|1": [
            'http://img.qqzhi.com/uploads/2018-12-23/085251409.jpg',
            'https://ss2.bdstatic.com/70cFvnSh_Q1YnxGkpoWK1HF6hhy/it/u=2175221156,3353011738&fm=27&gp=0.jpg',
            'https://ss1.baidu.com/6ONXsjip0QIZ8tyhnq/it/u=2720646439,3097064748&fm=85&app=63&f=JPEG?w=121&h=75&s=1591713342F866378C9CD08E030030A2',
            'http://i1.umei.cc/uploads/tu/201608/161/c16.jpg',
          ],
          name: '@cname()',
          ans: Random.cparagraph(),
          agree: '@natural(0, 300)',
          com: " @natural(0, 300)",
          date: Random.date('yyyy-MM-dd hh:mm'),
          praise: '@natural(0, 300)',
          "tocomment|1": true,
          comment: '@natural(0, 300)',
        }
      ]
    }]
});



