<template>

  <div class="userinfo" v-if="isLogin">
    <div class="us-top">
      <div class="logo" @click="touser(userInfo.uid)">
        <img :src="userInfo.head" alt="">
      </div>
      <div class="user" @click="touser(userInfo.uid)">
        <div class="user-name">{{userInfo.username}}</div>
        <div class="dec">{{userInfo.bio}}</div>
      </div>
    </div>
    <div class="us-bottom">
      <div class="dec-box">
        <div class="num">{{userInfo.question}}</div>
        <div class="describe">问题</div>
      </div>
      <div class="dec-box">
        <div class="num">{{userInfo.likenum}}</div>
        <div class="describe">收到赞</div>
      </div>
      <div class="dec-box">
        <div class="num">{{userInfo.fans}}</div>
        <div class="describe">粉丝</div>
      </div>
    </div>
  </div>

</template>

<script>
  export default {
    name: "userinfo",
    data() {
      return {
        isLogin:false,
        userInfo: {},
      }
    },
    mounted() {
      this.isLogin = this.$store.state.isLogin;
      this.userInfo = this.$store.state.userInfo;
    },
    methods: {
      //用户详情
      touser(uid){
        this.$router.push({name:'user',query:{otheruid:uid}})
      },
    }
  }
</script>

<style scoped lang="scss">
  .userinfo {
    width: 100%;
    height: 206px;
    padding: 20px 30px;
    box-sizing: border-box;
    background: rgba(255, 255, 255, 1);
    box-shadow: 0px 2px 9px 0px rgba(0, 0, 0, 0.12);
    border-radius: 6px;

    .us-top {
      display: flex;
      justify-content: flex-start;

      .logo {
        width: 72px;
        height: 72px;
        border-radius: 50%;
        overflow: hidden;
        cursor: pointer;

        img {
          width: 72px;
          height: 72px;
          border-radius: 50%;
          overflow: hidden;
        }
      }

      .user {
        cursor: pointer;
        margin-left: 10px;
        display: flex;
        flex-direction: column;
        justify-content: space-around;

        .user-name {
          font-size: 16px;
          font-weight: bold;
          color: rgba(51, 51, 51, 1);
          line-height: 18px;
        }

        .dec {
          width: 200px;
          font-size: 14px;
          font-weight: 400;
          color: rgba(153, 153, 153, 1);
          line-height: 18px;
          display: block;
          white-space: nowrap;
          overflow: hidden;
          text-overflow: ellipsis;
        }
      }

    }

    .us-bottom {
      margin-top: 40px;
      text-align: center;
      display: flex;
      justify-content: space-between;

      .dec-box {
        display: flex;
        flex-direction: column;

        .num {
          font-size: 24px;
          font-weight: bold;
          color: rgba(51, 51, 51, 1);
          line-height: 18px;
        }

        .describe {
          margin-top: 14px;
          font-size: 14px;
          font-weight: 400;
          color: rgba(153, 153, 153, 1);
          line-height: 18px;
        }
      }
    }
  }
</style>



