<template>
    <div class="fans">
      <ul>
        <li v-for="(item,index) in users" :key="index">
          <div class="left">
            <div class="img"><img :src="item.head" alt=""></div>
            <div class="info">
              <div class="title">{{item.title}}</div>
              <div class="dec">{{item.bio}}</div>
            </div>
          </div>
          <div class="right">
            <el-button type="danger" size="small">关注</el-button>
            <!--<el-button >已关注</el-button>-->
          </div>
        </li>
      </ul>
    </div>
</template>

<script>
    export default {
        name: "users",
      props:{
        users:Array
      }
    }
</script>

<style scoped lang="scss">
.fans{
  width: 100%;
  ul{
    width: 100%;
    display: flex;flex-direction: column;
    padding:0 20px;
    box-sizing: border-box;
    li{
      height: 82px;
      display: flex;
      justify-content: space-between;
      border-bottom: 1px solid rgba(165,165,165,1);
      .left{
        display: flex;
        .img{
          display: flex;
          justify-content: center;
          align-items: center;
          margin-left: 12px;
          margin-right: 20px;
          img{
            width: 40px;
            height: 40px;
            border-radius: 50%;
            overflow: hidden;
          }
        }
      }

        .info{
          display: flex;
          flex-direction: column;
          height: 100%;
          .title{
            margin-top: 15px;
            font-size:16px;
            width: 400px;
            font-weight:bold;
            color:rgba(51,51,51,1);
            line-height:25px;
            overflow: hidden;white-space: nowrap;text-overflow: ellipsis;
          }
          .dec{
            width: 500px;
            font-size:14px;
            font-weight:400;
            color:rgba(153,153,153,1);
            line-height:25px;
            overflow: hidden;white-space: nowrap;text-overflow: ellipsis;}
        }
        .right{
          display: flex;
          align-items: center;
        }

    }
  }
}
</style>



