<template>
  <div class="recommend">
    <div class="title-box">
      <div class="title">热门推荐</div>
      <!--<router-link to="#" class="more">更多</router-link>-->
    </div>
    <ul class="recom-box">
      <li v-for="(item,index) in recom" :key="index" class="recom-item">
        <div class="recom-title" @click="toessay(item.aid)" >{{item.title}}</div>
        <div class="recom-user">
          <div class="recom-left">
            <div class="recom-logo"  @click="touser(item.uid)">
              <img :src="item.head" alt="">
            </div>
            <div class="recom-name"  @click="touser(item.uid)">{{item.username}}</div>
            <div class="recom-label"  @click="touser(item.uid)"> {{item.label}}  </div>
          </div>
          <div class="recom-right">{{item.list.likenum}}&nbsp; 赞</div>
        </div>
        <div class="recom-art">
          <div class="recom-dec" v-html="item.list.content">
          </div>
          <div class="recom-img"  v-if="item.pic1">
            <img :src="item.pic1" alt="">
          </div>
        </div>
      </li>
    </ul>
  </div>
</template>

<script>
    export default {
        name: "recommend",
      data() {
        return {
          recom: [],
        };
      },
      mounted(){
        this.getarticle();
      },
      methods:{
        //用户详情
        touser(uid){
          this.$router.push({name:'user',query:{otheruid:uid}})
        },
        //  跳往页面详情
        toessay(aid) {
          this.$router.push({path: '/essay',query:{aid}})
        },
        //获取文章数据
        getarticle() {
          this.$post("index/data/question_hot").then(res => {
            this.recom=res.data;
            this.recom.map((val,index,arr)=>{
              val.head=this.$httpUrl+"/uc_server/avatar.php?uid="+val.uid+"&amp;size=big"
            });
         this.recom = JSON.parse(JSON.stringify(res.data));
          }).catch(err => {
            console.log(err);
          })
        },
      }
    }
</script>

<style scoped lang="scss">
  $main: #409eff !default;
  .recommend {
    width: 100%;
    background: rgba(255, 255, 255, 1);
    box-shadow: 0px 2px 9px 0px rgba(0, 0, 0, 0.12);
    border-radius: 6px;
    display: flex;
    flex-direction: column;

    .title-box {
      display: flex;
      justify-content: space-between;
      padding: 0 22px;
      box-sizing: border-box;
      height: 44px;
      line-height: 44px;
      border-bottom: 1px solid rgba(221, 221, 221, 1);

      .title {
        font-size: 16px;
        font-weight: 400;
        color: rgba(51, 51, 51, 1);
        line-height: 44px;
        position: relative;

        &:after {
          content: '';
          width: 100%;
          height: 4px;
          background: rgba(0, 150, 255, 1);
          border-radius: 2px;
          position: absolute;
          bottom: 0;
          left: 0;

        }
      }

      .more {
        width: 30px;
        height: 15px;
        font-size: 14px;
        font-weight: 400;
        color: rgba(153, 153, 153, 1);
        line-height: 44px;
      }
    }

    .recom-box {
      width: 100%;
      display: flex;
      flex-direction: column;
      padding: 0 20px;
      box-sizing: border-box;
      margin-bottom: 30px;

      .recom-item {
        cursor: pointer;
        .recom-title {

          width: 228px;
          font-size: 14px;
          font-weight: bold;
          color: rgba(51, 51, 51, 1);
          line-height: 44px;
          overflow: hidden;
          white-space: nowrap;
          text-overflow: ellipsis;
          &:hover{
            color: $main;
          }
        }

        .recom-user {
          display: flex;
          justify-content: space-between;
          margin-bottom: 12px;

          .recom-left {
            display: flex;
            justify-content: flex-start;

            .recom-logo {
              width: 20px;
              height: 20px;
              border-radius: 50%;
              overflow: hidden;
              cursor: pointer;

              img {
                width: 20px;
                height: 20px;cursor: pointer;
              }
            }

            .recom-name {
              padding: 0 8px;
              font-size: 12px;
              font-weight: 400;
              color: rgba(51, 51, 51, 1);
              line-height: 20px;cursor: pointer;
            }

            .recom-label {
              width: 96px;
              font-size: 12px;
              font-weight: 400;
              color: rgba(102, 102, 102, 1);
              line-height: 20px;cursor: pointer;
            }
          }

          .recom-right {
            width: 80px;
            height: 11px;
            font-size: 12px;
            font-weight: 400;
            color: rgba(153, 153, 153, 1);
            line-height: 20px;
            text-align: right;
          }
        }

        .recom-art {
          display: flex;
          justify-content: space-between;

          .recom-dec {
            width: 199px;
            max-height: 66px;
            font-size: 14px;
            font-weight: 400;
            color: rgba(153, 153, 153, 1);
            line-height: 22px;
            display: -webkit-box;
            -webkit-box-orient: vertical;
            -webkit-line-clamp: 3;
            overflow: hidden;
          }

          .recom-img {
            width: 96px;
            height: 66px;
            border-radius: 4px;
            overflow: hidden;

            img {
              width: 100%;
              height: 100%;
            }
          }

        }

      }
    }
  }
</style>



