<template>
  <div class="tag">
    <div class="title-box">
      <div class="title">推荐标签</div>
    </div>
    <ul class="label-box">
      <li class="label" @click="tolabel" v-for="(item,index) in labels">
        <div class="img" v-if="item.img">
          <img :src="item.img" alt="">
        </div>
        <div class="name">{{item.name}}</div>
      </li>
    </ul>
  </div>
</template>

<script>
  export default {
    name: "tag",
    data() {
      return {
        labels: []
        /* labels: [
           {
             img: 'https://ss1.baidu.com/9vo3dSag_xI4khGko9WTAnF6hhy/image/h%3D300/sign=a573495ce5f81a4c3932eac9e72a6029/2e2eb9389b504fc27c224b2debdde71190ef6d9d.jpg',
             name: '社会'
           },
           {
             img: 'https://ss1.bdstatic.com/70cFvXSh_Q1YnxGkpoWK1HF6hhy/it/u=873265023,1618187578&fm=27&gp=0.jpg',
             name: '娱乐'
           }, {
             img: 'https://ss1.baidu.com/9vo3dSag_xI4khGko9WTAnF6hhy/image/h%3D300/sign=1e6cee14c73d70cf53faac0dc8dcd1ba/024f78f0f736afc318638348bd19ebc4b74512ab.jpg',
             name: '体育'
           }, {
             img: 'https://ss1.baidu.com/9vo3dSag_xI4khGko9WTAnF6hhy/image/h%3D300/sign=a573495ce5f81a4c3932eac9e72a6029/2e2eb9389b504fc27c224b2debdde71190ef6d9d.jpg',

             name: '军事'
           }, {
             img: 'https://ss1.baidu.com/9vo3dSag_xI4khGko9WTAnF6hhy/image/h%3D300/sign=1e6cee14c73d70cf53faac0dc8dcd1ba/024f78f0f736afc318638348bd19ebc4b74512ab.jpg',

             name: '汽车'
           }, {
             img: 'https://ss1.baidu.com/9vo3dSag_xI4khGko9WTAnF6hhy/image/h%3D300/sign=a573495ce5f81a4c3932eac9e72a6029/2e2eb9389b504fc27c224b2debdde71190ef6d9d.jpg',

             name: '财经'
           }, {
             img: 'https://ss1.bdstatic.com/70cFvXSh_Q1YnxGkpoWK1HF6hhy/it/u=873265023,1618187578&fm=27&gp=0.jpg',

             name: '历史'
           },

         ]*/
      }
    },
    mounted() {
      this.getlabel()
    },
    methods: {
      //标签
      tolabel() {

      },
      //标签数据
      getlabel() {
        this.$post('index/data/label_hot').then(res => {
          console.log(res);
          this.labels = res.data;
        }).catch(err => {
          console.log(err);
        })
      }
    }
  }
</script>

<style scoped lang="scss">
  $main: #409eff !default;

  .tag {
    margin-top: 20px;
    width: 100%;
    background: rgba(255, 255, 255, 1);
    box-shadow: 0px 2px 9px 0px rgba(0, 0, 0, 0.12);
    border-radius: 6px;
    display: flex;
    flex-direction: column;

    .title-box {
      display: flex;
      justify-content: space-between;
      padding: 0 22px;
      box-sizing: border-box;
      height: 44px;
      line-height: 44px;
      border-bottom: 1px solid rgba(221, 221, 221, 1);

      &:hover .title{
        color: $main;
      }

      .title {
        font-size: 16px;
        font-weight: 400;
        color: rgba(51, 51, 51, 1);
        line-height: 44px;
        position: relative;

        &:after {
          content: '';
          width: 100%;
          height: 4px;
          background: rgba(0, 150, 255, 1);
          border-radius: 2px;
          position: absolute;
          bottom: 0;
          left: 0;

        }
      }


    }

    .label-box {
      width: 100%;
      display: flex;
      flex-direction: column;
      padding: 0 22px;
      box-sizing: border-box;
      margin-bottom: 20px;

      .label {
        cursor: pointer;
        margin-top: 16px;
        display: flex;
        justify-content: flex-start;

        .img {
          width: 40px;
          height: 40px;
          border-radius: 4px;
          overflow: hidden;

          img {
            width: 100%;
            height: 100%;
          }
        }

        .name {
          margin-left: 16px;
          line-height: 40px;
          font-size: 16px;
          font-weight: 400;
          color: rgba(51, 51, 51, 1);
        }
      }

    }
  }
</style>



