import Vue from 'vue'
import Router from 'vue-router'


Vue.use(Router);

export default new Router({
  routes: [
    {
      path: '/',
      name: 'index',
      meta: {requireAuth: false, keepAlive: false,title:'小盈问答'},
      component: resolve => require(['@/pages/index'], resolve),
      children:[
        {
          path: '/portal',
          name: 'portal',
          meta: {requireAuth: false, keepAlive: false,title:'小盈问答'},
          component: resolve => require(['@/pages/portal'], resolve),
        } ,
        //个人中心
        {
          path: '/user',
          name: 'user',
          meta: {requireAuth: true, keepAlive: false,title:'个人中心'},
          component: resolve => require(['@/pages/personal/user'], resolve)
        },
        //编辑用户信息
        {
          path: '/editUser',
          name: 'editUser',
          meta: {requireAuth: true, keepAlive: false,title:'编辑个人信息'},
          component: resolve => require(['@/pages/personal/editUser'], resolve)
        },
        //文章详情
        {
          path: '/essay',
          name: 'essay',
          meta: {requireAuth: false, keepAlive: false,title:'提问'},
          component: resolve => require(['@/pages/essay/essay'], resolve)
        },
        //标签详情页
        {
          path: '/label',
          name: 'label',
          meta: {requireAuth: false, keepAlive: false,title:'标签'},
          component: resolve => require(['@/pages/label/label'], resolve)
        },
        //搜索详情页
        {
          path: '/search',
          name: 'search',
          meta: {requireAuth: false, keepAlive: false,title:'搜索页面'},
          component: resolve => require(['@/pages/search/search'], resolve)
        },
        //问答
        {
          path: '/toanswer',
          name: 'toanswer',
          meta: {requireAuth: false, keepAlive: false,title:'问答'},
          component: resolve => require(['@/pages/answer/toanswer'], resolve)
        },
        //关于我们
        {
          path: '/about',
          name: 'about',
          meta: {requireAuth: false, keepAlive: true,title:'关于我们'},
          component: resolve => require(['@/pages/link/about'], resolve)
        },
        //版权声明
        {
          path: '/copyright',
          name: 'copyright',
          meta: {requireAuth: false, keepAlive: false,title:'版权声明'},
          component: resolve => require(['@/pages/link/copyright'], resolve)
        },
        //商业合作
        {
          path: '/business',
          name: 'business',
          meta: {requireAuth: false, keepAlive: true,title:'商业合作'},
          component: resolve => require(['@/pages/link/business'], resolve)
        },
      ]
    } ,
    //登录注册
    {
      path: '/loginmima',
      name: 'loginmima',
      meta: {requireAuth: false, keepAlive: false,title:'密码登录'},
      component: resolve => require(['@/pages/login/loginmima'], resolve)
    } , {
      path: '/loginphone',
      name: 'loginphone',
      meta: {requireAuth: false, keepAlive: false,title:'手机登录'},
      component: resolve => require(['@/pages/login/loginphone'], resolve)
    }, {
      path: '/register',
      name: 'register',
      meta: {requireAuth: false, keepAlive: false,title:'注册'},
      component: resolve => require(['@/pages/login/register'], resolve)
    },
    { //错误重定向
      path: '**',   // 错误路由
      redirect: 'portal'   //重定向
    }
  ]
})



